@import '../index.scss';

.timeOptions {
  margin: 0 16px;

  .time-options-container {
    display: flex;
    justify-content: space-between;

    .time-options-cell {
      button {
        color: $color1;
        font-weight: bold;
        border: 0;
        border-radius: 5px;
        margin: 0;
        padding: 8px;
        cursor: pointer;
        text-align: center;
        background-color: $color2;
        transition: 250ms;

        &:hover {
          opacity: 80%;
        }
      }
    }

    .active {
      button {
        color: $color2 !important;
        background-color: $color1 !important;
        border-radius: 5px;
      }
    }
  }

  .start-button-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;

    button {
      margin-top: 32px;
      align-self: center;
    }
  }
}

@media only screen and (min-width: 400px) {
  .timeOptions {
    width: 350px;
    align-self: center;
    justify-self: center;
  }
}
